import React from 'react';
import { Helmet } from 'react-helmet';
import {
  TimeDelta,
  Loading,
  getMultiplier,
  numberWithCommas,
  Footer,
} from './Functions.js';
import { useNavigate } from 'react-router-dom';
import { ReactComponent as RedExclamation } from './redexclamation.svg';
import 'tippy.js/dist/tippy.css'; // optional
import Tippy from '@tippyjs/react';

const NavigationBar = () => {
  return (
    <nav className='flex-wrap sm:flex sm:font-semibold justify-between px-5 sm:px-20 bg-secondary font-thin w-[100%]'>
      <div className='w-10 sm:w-20'>
        <a href='https://discord.gg/ej92B474Ej'>
          <img
            src='https://cdn.discordapp.com/icons/840150806682664970/39b968a6f46b5a7d48a2256d03caa6c6.webp'
            alt='SkyHub Logo'
          />
        </a>
      </div>
      <div className='flex justify-between w-[100%] sm:w-[33%]'>
        <div>
          <a
            className='text-white hover:text-primary'
            href='https://discord.gg/ej92B474Ej'
          >
            Join Discord
          </a>
        </div>
        <div>
          <a
            className='text-white hover:text-primary'
            href='https://discord.gg/ej92B474Ej'
          >
            Support Us
          </a>
        </div>
        <div>
          <a
            className='text-white hover:text-primary'
            href='https://discord.gg/ej92B474Ej'
          >
            Buy Guild Ads
          </a>
        </div>
      </div>
    </nav>
  );
};

class StatsHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      guildsTracked: 0,
      playersTracked: 0,
      isLoaded: false,
    };
  }

  render() {
    return (
      <header>
        <Helmet>
          <meta charSet='utf-8' />
          <meta property='og:title' content={'SkyBlock Guildleaderboard'} />
          <meta
            property='og:description'
            content={`Tracking ${this.state.guildsTracked} guilds with ${this.state.playersTracked} players`}
          />
        </Helmet>
        <h1 className='text-2xl text-center text-white bg-secondary sm:text-5xl'>
          Hypixel Skyblock Guild leaderboard
        </h1>
        <h1 className='pt-4 text-center text-white sm:text-2xl'>
          Tracking {this.state.guildsTracked} guilds with{' '}
          {this.state.playersTracked} players
        </h1>
        <h1 className='text-center text-white sm:text-1xl pt-'>
          Scammer Database provided by{' '}
          <a
            className='text-blue-500 underline'
            href='https://discord.gg/skyblock'
          >
            SkyBlockZ
          </a>
        </h1>
      </header>
    );
  }

  componentDidMount() {
    fetch('https://api.guildleaderboard.com/stats')
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            guildsTracked: result.guilds_tracked,
            playersTracked: result.players_tracked,
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  }
}

const Guild = (props) => {
  const navigate = useNavigate();
  const goRouteId = () => {
    navigate(`/guild/${props.id}`);
  };
  let element;

  if (props.scammers > 0) {
    element = (
      <th className='pr-2'>
        <Tippy
          // options
          // position="top"
          // animation={false}
          interactive={true}
          appendTo={document.body}
          duration={0}
          theme='tomato'
          content={
            <div className='block-inline'>
              <a
                className='text-blue-500 underline'
                href='https://discord.gg/skyblock'
              >
                SkyBlockZ
              </a>{' '}
              found {props.scammers} scammers in this guild.
            </div>
          }
        >
          <RedExclamation style={{ height: '1.5em' }} />
        </Tippy>
      </th>
    );
  } else {
    element = <th></th>;
  }

  return (
    <tr
      className={`${props.color} font-[Helvetica] hover:opacity-60 hover:cursor-pointer`} // text-[.9em] font-semibold
      onClick={goRouteId}
    >
      <th className='px-1 sm:px-6'>{props.position}</th>
      {element}
      <th className='text-left'>{props.name}</th>
      <th>
        <span className='px-1 font-normal bg-yellow-500 rounded-md sm:px-1'>
          {props.members}
        </span>
      </th>
      <th>
        <Tippy
          content={`${numberWithCommas(
            props.actual_weight
          )} Weight with a multiplier of ${numberWithCommas(
            getMultiplier(props.members)
          )}`}
        >
          <span className='px-1 font-normal bg-purple-700 rounded-md sm:px-1'>
            {numberWithCommas(props.average_weight)}
          </span>
        </Tippy>
      </th>
      <th>
        <Tippy
          content={`${props.name} is #${props.average_skills_index} in Average Skills`}
        >
          <span className='px-1 font-normal bg-blue-500 rounded-md sm:px-1'>
            {props.average_skills}
          </span>
        </Tippy>
      </th>
      <th>
        <Tippy
          content={`${props.name} is #${props.average_slayer_index} in Slayers`}
        >
          <span className='px-1 font-normal bg-red-500 rounded-md sm:px-1'>
            {numberWithCommas(Math.round(props.slayers))}
          </span>
        </Tippy>
      </th>
      <th>
        <Tippy
          content={`${props.name} is #${props.catacombs_index} in Catacombs`}
        >
          <span className='px-1 font-normal bg-green-400 rounded-md sm:px-1'>
            {props.catacombs}
          </span>
        </Tippy>
      </th>
      <th className='sm:px-5'>{props.capture_date}</th>
      <th></th>
    </tr>
  );
};

class Guilds extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      guildsJson: [],
      isLoaded: false,
      sortOn: 'average_weight',
      sortReversed: false,
      useMultiplier: true,
      sortedOnSlayer: [],
      sortedOnAverageSkill: [],
      sortedOnCatacombs: [],
    };
  }

  onSortClick(sortOn) {
    console.log(`Clicked ${sortOn}`);
    if (this.state.sortOn === sortOn) {
      this.setState({
        sortReversed: !this.state.sortReversed,
      });
    } else {
      this.setState({
        sortOn: sortOn,
        sortReversed: false,
      });
    }
  }
  sortOn(guild_data, sortOn) {
    let r = guild_data.slice();
    r.sort(function (a, b) {
      return b[sortOn] - a[sortOn];
    });
    return r;
  }

  render() {
    let guild_data = this.state.guildsJson.slice();
    if (!this.state.isLoaded) {
      return <Loading />;
    }
    let sortOn = this.state.sortOn;
    let sortReversed = this.state.sortReversed;

    guild_data.sort(function (a, b) {
      if (sortReversed === true) {
        return a[sortOn] - b[sortOn];
      } else {
        return b[sortOn] - a[sortOn];
      }
    });
    let guilds = [];
    for (const i in guild_data) {
      let guild_json = guild_data[i];
      // get the average_slayer, average_skills, average_catacombs index of the guild in the guild_data array
      let color = i % 2 === 0 ? 'bg-tertiary' : '';
      guilds.push(
        <Guild
          position={parseInt(i) + 1}
          name={guild_json.guild_name}
          id={guild_json.guild_id}
          members={guild_json.players}
          average_weight={guild_json.average_weight}
          actual_weight={guild_json.actual_weight}
          average_skills={guild_json.average_skills}
          slayers={guild_json.average_slayer}
          average_slayer_index={
            this.state.sortedOnSlayer.findIndex(
              (guild) => guild.guild_id === guild_json.guild_id
            ) + 1
          }
          catacombs_index={
            this.state.sortedOnCatacombs.findIndex(
              (guild) => guild.guild_id === guild_json.guild_id
            ) + 1
          }
          average_skills_index={
            this.state.sortedOnAverageSkill.findIndex(
              (guild) => guild.guild_id === guild_json.guild_id
            ) + 1
          }
          catacombs={guild_json.average_catacombs}
          capture_date={new TimeDelta(
            guild_json.time_difference
          ).toNiceString()}
          scammers={guild_json.scammers}
          key={guild_json.guild_id}
          color={color}
        />
      );
    }
    return (
      <table className='text-white bg-primary text-center rounded-md text-[.4em] sm:text-lg mx-auto'>
        <tbody>
          <tr>
            <th>#</th>
            <th></th>
            <th className='tracking-[.1em] text-left'>Guilds</th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('players');
              }}
            >
              Member
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('average_weight');
              }}
            >
              Weight
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('average_skills');
              }}
            >
              Average Skills
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('average_slayer');
              }}
            >
              Slayers
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('average_catacombs');
              }}
            >
              Catacombs
            </th>
            <th>Last updated</th>
          </tr>
          {guilds}
        </tbody>
      </table>
    );
  }

  componentDidMount() {
    fetch('https://api.guildleaderboard.com/leaderboard')
      .then((res) => res.json())
      .then(
        (result) => {
          if (this.state.useMultiplier) {
            result.forEach((guild) => {
              guild.actual_weight = guild.average_weight;
              guild.average_weight =
                guild.average_weight * getMultiplier(guild.players);
            });
          }

          this.setState({
            isLoaded: true,
            guildsJson: result,
            sortedOnSlayer: this.sortOn(result, 'average_slayer'),
            sortedOnAverageSkill: this.sortOn(result, 'average_skills'),
            sortedOnCatacombs: this.sortOn(result, 'catacombs'),
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            isLoaded: true,
            error,
          });
        }
      );
  }
}

function Home() {
  // scrollbar
  return (
    <div class='bg-secondary min-h-screen space-y-10 pt-7 sm:h-96 overflow-y-auto scrollbar'>
      <NavigationBar />
      <StatsHeader />
      <Guilds />
      <Footer />
    </div>
  );
}

export default Home;
