export class Loading extends React.Component {
	render() {
		return (
			<div className='text-[3em] font-semibold text-white'>
				<svg
					xmlns='http://www.w3.org/2000/svg'
					class='mx-auto'
					width='64px'
					height='64px'
					viewBox='0 0 100 100'
					preserveAspectRatio='xMidYMid'
				>
					<circle
						cx='50'
						cy='50'
						fill='none'
						stroke='#0a0a0a'
						strokeWidth='10'
						r='35'
						strokeDasharray='164.93361431346415 56.97787143782138'
					>
						<animateTransform
							attributeName='transform'
							type='rotate'
							repeatCount='indefinite'
							dur='1s'
							values='0 50 50;360 50 50'
							keyTimes='0;1'
						/>
					</circle>
				</svg>
			</div>
		);
	}
}

export class TimeDelta {
	constructor(str) {
		this.days = 0;
		if (str.includes(", ")) {
			[this.daystr, this.rest] = str.split(", ")
			this.days = parseInt(this.daystr.split(' ')[0]);
		} else {
			this.rest = str;
		}

		[this.seconds, this.minutes, this.hours] = this.rest.split(':').reverse();
		this.seconds = parseInt(this.seconds); // Imagine looping
		this.minutes = parseInt(this.minutes);
		this.hours = parseInt(this.hours);
	}
	toString() {
		return `${this.hours}:${this.minutes}:${this.seconds}`;
	}
	toNiceString() {
		if (this.hours > 1) {
			return `${this.hours} hours ago`;
		} else {
			return `${this.minutes} minutes ago`;
		}
	}

	toNiceStringWDays() {
		if (this.days > 0) {
			return `${this.days} days ago`;
		} else {
			return this.toNiceString();
		}
	}
	toLongDate() {
		return new Date(Date.now() - this.toMS());
	}

	toSeconds() {
		return this.seconds + this.minutes * 60 + this.hours * 3600 + this.days * 86400;
	}
	toMS() {
		return this.toSeconds() * 1000;
	}
}

export function getMultiplier(members) {
	let frequency = Math.sin(members / (125 / 0.927296)) + 0.2
	return members / 125 + (1 - members / 125) * frequency
}
