import Home from './Home';
import Guild from './Guild';
import React from 'react';
import { Routes, Route } from 'react-router-dom';

function App() {
    return (
        <Routes>
            <Route path='/' element={<Home />}></Route>
            <Route path="/guild/:guildId" element={<Guild />}></Route>
            <Route path='*' element={<Home />}></Route>            
        </Routes>
    );
}

export default App;
