import React, { useRef } from 'react';
import { TimeDelta, numberWithCommas, getMultiplier } from './Functions.js';
import { useNavigate } from 'react-router-dom';
import { Loading, Footer } from './Functions.js';
import { CustomChart, CustomChart2 } from './Chart.js';
import { Helmet } from 'react-helmet';
import { followCursor } from 'tippy.js';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css'; // optional
import ReactTags from 'react-tag-autocomplete';

const StatBlockTop = (props) => {
  const element = (
    <div className='inline-block px-1 py-2 text-sm font-normal text-white'>
      <span className='p-2 rounded-l-lg bg-primary'>{`${props.name} `}</span>
      <span className={`${props.color} rounded-r-lg p-2`}>{props.value}</span>
    </div>
  );
  if (props.tippy) {
    return <Tippy content={props.tippy}>{element}</Tippy>;
  } else {
    return element;
  }
};

const GuildHeader = (props) => {
  const navigate = useNavigate();
  const backButton = () => {
    navigate(`/`);
  };

  if (!props.guildJson) {
    return (
      <div className='text-center font-[Helvetica]'>
        <Loading />
        <div class='w-max mx-auto'>
          <button
            onClick={backButton}
            className='flex bg-secondary text-[1.2em] text-center text-gray-200 opacity-60 drop-shadow-lg hover:opacity-75 hover:scale-105 px-3 py-2 rounded-md border border-gray-800'
          >
            <div className='pr-2'>
              <svg
                xmlns='http://www.w3.org/2000/svg'
                class='h-6 w-6'
                fill='none'
                viewBox='0 0 24 24'
                stroke='currentColor'
                strokeWidth='2'
              >
                <path
                  strokeLinecap='round'
                  strokeLinejoin='round'
                  d='M11 17l-5-5m0 0l5-5m-5 5h12'
                />
              </svg>
            </div>
            <div>Back to guild leaderboards</div>
          </button>
        </div>
      </div>
    );
  }
  let element;
  if (props.selectedPage === 'players') {
    element = (
      <h1 className='pb-2 text-center text-white sm:text-1xl'>
        Scammer Database provided by{' '}
        <a
          className='text-blue-500 underline'
          href='https://discord.gg/skyblock'
        >
          SkyBlockZ
        </a>
      </h1>
    );
  } else {
    element = '';
  }
  let guild_discord;
  if (props.guildJson.discord != null) {
    guild_discord = (
      <button className='bg-blue-800 text-[1.2em] text-center text-gray-200 drop-shadow-lg opacity-95 hover:opacity-100 hover:scale-105 px-3 py-2 rounded-md border border-gray-800'>
        <i class='fab fa-discord'></i>
        <span className='pl-3'>
          <a href={`https://discord.gg/${props.guildJson.discord}`}>Guild Discord</a>
        </span>
      </button>
    );
  } else {
    guild_discord = (
      <button className='bg-blue-900 text-[1.2em] text-center text-gray-200 drop-shadow-lg opacity-25 px-3 py-2 rounded-md border border-gray-800'>
        <i class='fab fa-discord'></i>
        <span className='pl-3'>
          <a href='https://discord.gg/ej92B474Ej'>Submit Guild Discord</a>
        </span>
      </button>
    );
  }
  return (
    <div className='text-center font-[Helvetica]'>
      <Helmet>
        <meta charSet='utf-8' />
        <meta property='og:title' content={props.guildJson.guild_name} />
        <meta
          property='og:description'
          content={` Avg Skill Level: ${props.guildJson.average_skills}
						 Average Senither Weight: ${numberWithCommas(props.guildJson.average_weight)}
						 Average Catacombs: ${props.guildJson.average_catacombs}
						 Average Slayer: ${numberWithCommas(props.guildJson.average_slayer)}
						 Members: ${props.guildJson.players.length}`}
        />
      </Helmet>
      <h1 className='text-[1.8em] sm:text-[3em] font-semibold text-white'>
        {props.guildJson.guild_name}
      </h1>
      <div className='p-2 text-center'>
        <StatBlockTop
          color='bg-purple-700'
          value={numberWithCommas(
            props.guildJson.average_weight *
              getMultiplier(props.guildJson.players.length)
          )}
          name='Weight'
          tippy={`${numberWithCommas(
            props.guildJson.average_weight
          )} Weight with a multiplier of ${numberWithCommas(
            getMultiplier(props.guildJson.players.length)
          )}.`}
        />
        <StatBlockTop
          color='bg-blue-500'
          value={props.guildJson.average_skills}
          name='Skill Average'
        />
        <StatBlockTop
          color='bg-green-500'
          value={props.guildJson.average_catacombs}
          name='Catacombs'
        />
        <StatBlockTop
          color='bg-red-500'
          value={numberWithCommas(props.guildJson.average_slayer)}
          name='Slayer'
        />
        <StatBlockTop
          color='bg-yellow-500'
          value={props.guildJson.players.length}
          name='Members'
        />
      </div>
      <div className='py-2'>{guild_discord}</div>
      {element}
      <div class='w-max mx-auto'>
        <button
          onClick={backButton}
          className='flex bg-secondary text-[1.2em] text-center text-gray-200 opacity-60 drop-shadow-lg hover:opacity-75 hover:scale-105 px-3 py-2 rounded-md border border-gray-800'
        >
          <div class='pr-2'>
            <svg
              xmlns='http://www.w3.org/2000/svg'
              class='h-6 w-6'
              fill='none'
              viewBox='0 0 24 24'
              stroke='currentColor'
              strokeWidth='2'
            >
              <path
                strokeLinecap='round'
                strokeLinejoin='round'
                d='M11 17l-5-5m0 0l5-5m-5 5h12'
              />
            </svg>
          </div>
          <div>Back to guild leaderboards</div>
        </button>
      </div>
    </div>
  );
};

const Player = (props) => {
  let player_data = props.player_data;

  const ref = useRef();

  let element;
  let dropdown;

  dropdown = (
    <Tippy
      reference={ref}
      interactive={true}
      appendTo={document.body}
      theme={'white'}
      hideOnClick={true}
      trigger={'click'}
      content={
        <div className='p-5'>
          <div className='pb-3 text-lg font-bold'>
            {`${props.player_data.name}`}'s external links:{' '}
          </div>
          <div class='grid grid-rows-3 grid-flow-col gap-2 text-[1em]'>
            <div>
              <button
                className='px-3 py-1 font-semibold text-green-600 bg-green-500 border-green-500 rounded-md hover:opacity-[75%] hover:border-green-700'
                onClick={() =>
                  window.open(
                    `https://sky.shiiyu.moe/${props.player_data.name}`
                  )
                }
              >
                SkyCrypt
              </button>
            </div>
            <div>
              <button
                className='px-3 py-1 font-semibold text-orange-700 bg-orange-600 border-orange-600 rounded-md hover:opacity-[75%] hover:border-orange-700'
                onClick={() =>
                  window.open(
                    `https://plancke.io/hypixel/player/stats/${props.player_data.name}`
                  )
                }
              >
                Plancke
              </button>
            </div>
            <div>
              <button
                className='px-3 py-1 font-semibold text-gray-700 bg-gray-600 border-gray-600 rounded-md hover:opacity-[75%] hover:border-gray-700'
                onClick={() =>
                  window.open(
                    `https://auctions.craftlink.xyz/players/${props.player_data.name}`
                  )
                }
              >
                Hyauctions
              </button>
            </div>
          </div>
        </div>
      }
    />
  );
  if (player_data.scam_reason) {
    element = (
      <Tippy
        reference={ref}
        theme='tomato'
        interactive={true}
        appendTo={document.body}
        followCursor='horizontal'
        plugins={[followCursor]}
        content={
          <>
            <div>{player_data.scam_reason}</div>
            <div>
              Scammer Database by{' '}
              <a
                className='text-blue-500 underline'
                href='https://discord.gg/skyblock'
              >
                SkyBlockZ
              </a>
            </div>
          </>
        }
      />
    );
  } else {
    element = '';
  }

  return (
    <>
      <tr
        className={`${props.color} font-[Helvetica] hover:opacity-60 hover:cursor-pointer`}
        ref={ref}
      >
        <th className='px-1 sm:px-4'>{props.position}</th>
        <th className='pr-1'>
          <img
            src={`https://mc-heads.net/avatar/${player_data.name}`}
            alt=''
            className='w-3 sm:w-8'
          />
        </th>
        <th className='pr-[2em] text-left'>{player_data.name}</th>
        <th>
          <span className='px-2 font-normal bg-purple-700 rounded-md'>
            {numberWithCommas(player_data.weight)}
          </span>
        </th>
        <th>
          <span className='px-2 font-normal bg-blue-500 rounded-md'>
            {player_data.average_skill}
          </span>
        </th>
        <th>
          <span className='px-2 font-normal bg-red-500 rounded-md'>
            {numberWithCommas(Math.round(player_data.total_slayer))}
          </span>
        </th>
        <th>
          <span className='px-2 font-normal bg-green-400 rounded-md'>
            {player_data.catacomb}
          </span>
        </th>
        <th className='sm:px-5'>
          {new TimeDelta(player_data.time_difference).toNiceString()}
        </th>
        <th></th>
      </tr>
      {element}
      {dropdown}
    </>
  );
};

class Players extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sortOn: 'weight',
      sortReversed: false,
    };
  }

  onSortClick(sortOn) {
    if (this.state.sortOn === sortOn) {
      this.setState({
        sortReversed: !this.state.sortReversed,
      });
    } else {
      this.setState({
        sortOn: sortOn,
        sortReversed: false,
      });
    }
  }

  render() {
    if (!this.props.guildJson) {
      return <Loading />;
    }
    let players_data = this.props.guildJson.players.slice();
    let sortOn = this.state.sortOn;
    let sortReversed = this.state.sortReversed;
    let players = [];

    players_data.sort(function (a, b) {
      if (sortReversed === true) {
        return a[sortOn] - b[sortOn];
      } else {
        return b[sortOn] - a[sortOn];
      }
    });

    for (const i in players_data) {
      let player_data = players_data[i];
      let color = i % 2 === 0 ? 'bg-tertiary' : '';
      if (player_data.scam_reason) {
        color = 'bg-scamred';
      }
      players.push(
        <Player
          key={player_data.uuid}
          player_data={player_data}
          position={parseInt(i) + 1}
          color={color}
        />
      );
    }

    return (
      <table className='text-white bg-primary text-center rounded-md text-[.45em] overflow-x-auto sm:text-lg mx-auto'>
        <tbody>
          <tr>
            <th>#</th>
            <th></th>
            <th className='text-left'>Name</th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('weight');
              }}
            >
              Weight
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('average_skill');
              }}
            >
              Average Skills
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('total_slayer');
              }}
            >
              Slayers
            </th>
            <th
              className='hover:cursor-pointer'
              onClick={() => {
                this.onSortClick('catacomb');
              }}
            >
              Catacombs
            </th>
            <th>Last updated</th>
          </tr>
          {players}
        </tbody>
      </table>
    );
  }
}

class Metrics extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      width: 0,
      height: 0,
    };
  }

  updateDimensions = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  };

  render() {
    if (!this.props.isLoaded) {
      return <Loading />;
    }
    let categories = this.props.categories;
    let guild_sorted_data = this.props.guild_sorted_data;
    let inline;
    if (window.screen.width <= 800) {
      inline = '';
    } else {
      inline = 'inline-block w-1/3';
    }
    return (
      <div>
        <div class='text-center'>
          <div className={`${inline} bg-primary rounded-md m-2`}>
            <CustomChart
              categories={categories}
              data={guild_sorted_data.average_weight.data}
              title={'Average Weight'}
              width={'100%'}
            />
          </div>
          <div className={`${inline} bg-primary rounded-md m-2`}>
            <CustomChart
              categories={categories}
              data={guild_sorted_data.average_skills.data}
              title={'Average Skills'}
              width={'100%'}
            />
          </div>
        </div>
        <div class='text-center'>
          <div className={`${inline} bg-primary rounded-md m-2`}>
            <CustomChart
              categories={categories}
              data={guild_sorted_data.average_catacombs.data}
              title={'Average Catacombs'}
              width={'100%'}
            />
          </div>
          <div className={`${inline} bg-primary rounded-md m-2`}>
            <CustomChart
              categories={categories}
              data={guild_sorted_data.average_slayer.data}
              title={'Average Slayer'}
              width={'100%'}
            />
          </div>
        </div>

        <div class='text-center'>
          <div className={`${inline} bg-primary rounded-md m-2`}>
            <CustomChart
              categories={categories}
              data={guild_sorted_data.member_count.data}
              title={'Members'}
              width={'100%'}
            />
          </div>
        </div>
      </div>
    );
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions);
  }
}

class JoinLog extends React.Component {
  render() {
    let color;
    if (this.props.type === 'Joined') {
      color = 'bg-green-500';
    } else {
      color = 'bg-red-500';
    }
    return (
      <div className='bg-tertiary p-2 inline text-white text-[0.6rem] md:text-xl rounded-md'>
        <div className='inline-block w-2/3 text-left lg:w-1/2'>
          <p
            className={`inline-block ${color} mr-1 my-2 py-0.5 px-2 rounded-lg `}
          >
            {this.props.type}
          </p>
          <p className='inline-block'>{this.props.name}</p>
        </div>
        <div className='inline-block justify-end text-right rounded-xl py-0.5 px-2'>
          {this.props.time_difference.toNiceStringWDays().toString()}
        </div>
      </div>
    );
  }
}

class JoinLogs extends React.Component {
  render() {
    if (!this.props.guildJson) {
      return <Loading />;
    }

    let logsElements = [];
    // loop through guild json
    for (const i in this.props.guildJson) {
      let log = this.props.guildJson[i];
      let time_difference = new TimeDelta(log.date);
      for (const i in log.joined) {
        let name = log.joined[i];
        logsElements.push(
          <JoinLog
            key={`${name}:${log.date}`}
            type={'Joined'}
            name={name}
            time_difference={time_difference}
          />
        );
      }
      for (const i in log.left) {
        let name = log.left[i];
        logsElements.push(
          <JoinLog
            key={`${name}:${log.date}`}
            type={'Left'}
            name={name}
            time_difference={time_difference}
          />
        );
      }
    }

    return <div className='text-center'>{logsElements}</div>;
  }
}

