class CompareGuilds extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      width: 0,
      height: 0,
      tags: [],
      suggestions: [],
      all_series: {},
      autoCompleteLoaded: false,
      change: 0,
    };
    this.reactTags = React.createRef();
  }

  onDelete(i) {
    const tags = this.state.tags.slice(0);
    let guild_id = tags.splice(i, 1)[0].id;
    // remove guild from guild_sorted_data
    let all_series = this.state.all_series;
    delete all_series[guild_id];
    this.setState({
      tags: tags,
      all_series: all_series,
      change: this.state.change + 1,
    });
  }

  onAddition(tag) {
    const tags = [].concat(this.state.tags, tag);
    let guildid = tag.id;

    fetch(`https://api.guildleaderboard.com/metrics/${guildid}?format=true`)
      .then((res) => res.json())
      .then(
        (result) => {
          let all_series = this.state.all_series;
          all_series[guildid] = result;
          this.setState({
            tags: tags,
            all_series: all_series,
            change: this.state.change + 1,
          });

          // this.addGuildMetrics(result)
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            metricsIsLoaded: true,
            error,
          });
        }
      );
  }

  getSeries(wanted_key) {
    let return_data = [];
    for (let key in this.state.all_series) {
      this.state.all_series[key][wanted_key].data.forEach(function (item) {
        if (typeof item[0] === 'string') {
          let time_difference = new TimeDelta(item[0]);
          item[0] = Date.now() - time_difference.toMS();
        }
      });

      return_data.push(this.state.all_series[key][wanted_key]);
    }
    return return_data;
  }

  render() {
    if (!this.props.isLoaded || !this.state.all_series) {
      return <Loading />;
    }

    return (
      <div>
        <div className='text-center'>
          <div className='inline-block p-1 w-[90%] md:w-2/3 text-left mb-20'>
            <ReactTags
              ref={this.reactTags}
              tags={this.state.tags}
              suggestions={this.state.suggestions}
              noSuggestionsText='No matching guilds found'
              onDelete={this.onDelete.bind(this)}
              onAddition={this.onAddition.bind(this)}
              placeholderText='Add a guild'
            />
          </div>
        </div>
        <div className='text-center'>
          <div className='p-4 m-2 rounded-md md:inline-block md:w-2/3 bg-primary'>
            <CustomChart2
              series={this.getSeries('average_weight_data')}
              title={'Average Weight'}
              width={'100%'}
              key={this.state.change}
            />
          </div>
          <div className='p-2 m-2 rounded-md md:inline-block md:w-2/3 bg-primary'>
            <CustomChart2
              series={this.getSeries('average_skills_data')}
              title={'Average Skills'}
              width={'100%'}
              key={this.state.change}
            />
          </div>
          <div className='m-2 rounded-md md:inline-block md:w-2/3 bg-primary'>
            <CustomChart2
              series={this.getSeries('average_catacombs_data')}
              title={'Average Catacombs'}
              width={'100%'}
              key={this.state.change}
            />
          </div>
          <div className='m-2 rounded-md md:inline-block md:w-2/3 bg-primary'>
            <CustomChart2
              series={this.getSeries('average_slayer_data')}
              title={'Average Slayer'}
              width={'100%'}
              key={this.state.change}
            />
          </div>
          <div className='m-2 rounded-md md:inline-block md:w-2/3 bg-primary'>
            <CustomChart2
              series={this.getSeries('member_count_data')}
              title={'Members'}
              width={'100%'}
              key={this.state.change}
            />
          </div>
        </div>
      </div>
    );
  }

  updateDimensions = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  };

  componentDidMount() {
    window.addEventListener('resize', this.updateDimensions);
    this.onAddition({ id: this.props.guild_id, name: this.props.guild_name });
    fetch(`https://api.guildleaderboard.com/autocomplete`)
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            autoCompleteLoaded: true,
            suggestions: result,
          });
          if (result === null) {
            window.location.href = '/';
          }
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            autoCompleteLoaded: true,
            error,
          });
        }
      );
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions);
  }
}
class Guild extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      playerGuildJson: null,
      playersIsLoaded: false,
      selectedPage: 'players',

      metricsGuildJson: null,
      metricsIsLoaded: false,
      guild_sorted_data: {
        average_weight: {
          data: [],
        },
        average_skills: {
          data: [],
        },
        average_catacombs: {
          data: [],
        },
        average_slayer: {
          data: [],
        },
        member_count: {
          data: [],
        },
      },
      categories: [],

      historyGuildJson: null,
      historyIsLoaded: false,
    };
  }
  handleSwitchClick(selected) {
    this.setState({
      selectedPage: selected,
    });
  }

  renderComponent() {
    if (this.state.selectedPage === 'players') {
      return <Players guildJson={this.state.playerGuildJson} />;
    } else if (this.state.selectedPage === 'metrics') {
      if (!this.state.metricsIsLoaded) {
        this.loadMetrics();
      }
      return (
        <Metrics
          guild_sorted_data={this.state.guild_sorted_data}
          categories={this.state.categories}
          isLoaded={this.state.metricsIsLoaded}
        />
      );
    } else if (this.state.selectedPage === 'history') {
      if (!this.state.historyIsLoaded) {
        this.loadHistory();
      }
      return <JoinLogs guildJson={this.state.historyGuildJson} />;
    } else if (this.state.selectedPage === 'compare') {
      if (!this.state.metricsIsLoaded) {
        this.loadMetrics();
      }
      return (
        <CompareGuilds
          metricsGuildJson={this.state.metricsGuildJson}
          isLoaded={this.state.metricsIsLoaded}
          guild_id={this.state.playerGuildJson.guild_id}
          guild_name={this.state.playerGuildJson.guild_name}
        />
      );
    }
  }

  render() {
    return (
      <div className='min-h-screen space-y-10 overflow-y-auto bg-secondary pt-7 sm:h-96 scrollbar'>
        <GuildHeader
          guildJson={this.state.playerGuildJson}
          selectedPage={this.state.selectedPage}
        />
        <div className='text-center font-[Helvetica]'>
          <div className='inline-block p-1'>
            <button
              className='bg-primary text-[1.2em] text-gray-200 opacity-60 drop-shadow-lg enabled:hover:opacity-75 enabled:hover:scale-105 px-3 py-2 rounded-md border border-gray-800 disabled:bg-tertiary disabled:scale-95'
              onClick={(i) => this.handleSwitchClick('players')}
              disabled={this.state.selectedPage === 'players'}
            >
              {'Show Players'}
            </button>
          </div>
          <div className='inline-block p-1'>
            <button
              className='bg-primary text-[1.2em] text-gray-200 opacity-60 drop-shadow-lg enabled:hover:opacity-75 enabled:hover:scale-105 px-3 py-2 rounded-md border border-gray-800 disabled:bg-tertiary disabled:scale-95'
              onClick={(i) => this.handleSwitchClick('metrics')}
              disabled={this.state.selectedPage === 'metrics'}
            >
              {'Show Metrics'}
            </button>
          </div>
          <div className='inline-block p-1'>
            <button
              className='bg-primary text-[1.2em] text-gray-200 opacity-60 drop-shadow-lg enabled:hover:opacity-75 enabled:hover:scale-105 px-3 py-2 rounded-md border border-gray-800 disabled:bg-tertiary disabled:scale-95'
              onClick={(i) => this.handleSwitchClick('history')}
              disabled={this.state.selectedPage === 'history'}
            >
              {'View History'}
            </button>
          </div>
          <div className='inline-block p-1'>
            <button
              className='bg-primary text-[1.2em] text-gray-200 opacity-60 drop-shadow-lg enabled:hover:opacity-75 enabled:hover:scale-105 px-3 py-2 rounded-md border border-gray-800 disabled:bg-tertiary disabled:scale-95'
              onClick={(i) => this.handleSwitchClick('compare')}
              disabled={this.state.selectedPage === 'compare'}
            >
              {'Compare Guild'}
            </button>
          </div>
        </div>
        {this.renderComponent()}
        <Footer />
      </div>
    );
  }

  loadMetrics() {
    let paths = window.location.href.split('/');
    let guildid = paths[paths.length - 1];

    fetch(`https://api.guildleaderboard.com/metrics/${guildid}`)
      .then((res) => res.json())
      .then(
        (result) => {
          let guild_metrics = result;

          let categories = [];
          let guild_sorted_data = this.state.guild_sorted_data;

          for (const i in guild_metrics) {
            let guild_metric = guild_metrics[i];
            let time_difference = new TimeDelta(guild_metric.time_difference);
            let timestamp = new Date(Date.now() - time_difference.toMS());
            categories.push(
              `${timestamp.getDate()} ${timestamp.toLocaleString('default', {
                month: 'short',
              })}`
            );

            guild_sorted_data.average_weight.data.push(
              guild_metric.average_weight
            );
            guild_sorted_data.average_skills.data.push(
              guild_metric.average_skills
            );
            guild_sorted_data.average_catacombs.data.push(
              guild_metric.average_catacombs
            );
            guild_sorted_data.average_slayer.data.push(
              guild_metric.average_slayer
            );
            guild_sorted_data.member_count.data.push(guild_metric.member_count);
          }
          this.setState({
            metricsIsLoaded: true,
            metricsGuildJson: guild_metrics,
            guild_sorted_data: guild_sorted_data,
            categories: categories,
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            metricsIsLoaded: true,
            error,
          });
        }
      );
  }

  loadHistory() {
    let paths = window.location.href.split('/');
    let guildid = paths[paths.length - 1];

    fetch(`https://api.guildleaderboard.com/history/${guildid}`)
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            historyIsLoaded: true,
            historyGuildJson: result,
          });
          if (result === null) {
            window.location.href = '/';
          }
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            historyIsLoaded: true,
            error,
          });
        }
      );
  }

  componentDidMount() {
    let paths = window.location.href.split('/');
    let guildid = paths[paths.length - 1];

    fetch(`https://api.guildleaderboard.com/guild/${guildid}`)
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            playersIsLoaded: true,
            playerGuildJson: result,
          });
          if (result === null) {
            window.location.href = '/';
          }
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          console.log(error);
          this.setState({
            playersIsLoaded: true,
            error,
          });
        }
      );
  }
}

export default Guild;
